<template>
  <div :class="$root.themeHomeChange === '1' ? 'tablePlan_light' : 'tablePlan'">
    <!-- <div class="header">风险监测情况</div> -->
    <el-form
      :inline="true"
      class="selectBox"
      :model="params">
      <el-button type="primary" style="float: left;" @click="generateReport">生成报告</el-button>
      <span style="float: right;">
        <el-form-item label="报告类型: ">
          <el-select
            size="mini"
            v-model="params.bglx"
            placeholder="请选择"
            style="width: 150px;"
          >
            <el-option label="全部" value="全部"></el-option>
            <el-option label="年度报告" value="年度报告"></el-option>
            <el-option label="半年报告" value="半年报告"></el-option>
            <el-option label="季度报告" value="季度报告"></el-option>
            <el-option label="月度报告" value="月度报告"></el-option>
            <el-option label="自定义报告" value="自定义报告"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="责任部门: ">
          <el-select
            size="mini"
            v-model="params.zrbm"
            placeholder="请选择"
            style="width: 150px;"
            clearable
          >
            <el-option label="办公室" value="办公室"></el-option>
            <el-option label="合规与监审部" value="合规与监审部"></el-option>
            <el-option label="系统规划部" value="系统规划部"></el-option>
            <el-option label="资产管理部" value="资产管理部"></el-option>
            <el-option label="市场营销部" value="市场营销部"></el-option>
            <el-option label="安全监管部" value="安全监管部"></el-option>
            <el-option label="人力资源部" value="人力资源部"></el-option>
            <el-option label="党建工作部" value="党建工作部"></el-option>
            <el-option label="创新与数字化部" value="创新与数字化部"></el-option>
            <el-option label="财务部" value="财务部"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="报告生成时间">
          <el-date-picker
            v-model="params.bgscsj"
            size="mini"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy/MM/dd"
            style="width: 220px;"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-input
            placeholder="请输入关键字"
            suffix-icon="el-icon-search"
            v-model="params.keyVal"
            size="mini"
          >
          </el-input>
        </el-form-item>
      </span>
    </el-form>
    <div class="content">
      <el-table
        :data="tableData.filter(item => {
          let keyVal = false;
          for(let key in item) {
            if(item[key].includes(params.keyVal)) {
              keyVal = item[key].includes(params.keyVal);
              break;
            }
          }
          let judge = [
            (params.bglx == '全部' ? true : item.type == params.bglx),
            (params.keyVal ? keyVal : true),
            (params.zrbm ? item.zrbm == params.zrbm : true),
            (params.bgscsj ? (new Date(params.bgscsj[0]) <= new Date(item.bgscsj) && new Date(params.bgscsj[1]) >= new Date(item.bgscsj) ) : true),
          ];
          return !judge.includes(false);
        })"
        style="width: 100%"
        :height="tableHeight"
        tooltip-effect="light">
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="id"
          label="报告ID"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="name"
          label="报告名称"
          min-width="160"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="type"
          label="报告种类"
          width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
